import React from 'react';
import styled from 'styled-components';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import ArrowBackIcon from '@material-ui/icons/ArrowBackSharp';
import FileCopySharpIcon from '@material-ui/icons/FileCopySharp';
import { Link } from '@reach/router';

import { Layout, Wrapper, Arrow, Screen } from '../components';
import Drama from '../models/Drama';
import Person from '../models/Person';
import OuterChain from '../models/OuterChain';
import dramaDefault from '../utils/drama';

interface GenerateState {
  perview: string;
}

const BackBlack = styled.div`
  background-color: #424242;
  background-image: url('https://www.transparenttextures.com/patterns/iron-grip.png');
  padding: 50px;
`;

export default class Preview extends React.Component<{}, GenerateState> {
  constructor(props: any) {
    super(props);
    let drama: Drama;
    if (typeof window !== `undefined`) {
      const dramaJson: any = window.localStorage.getItem('drama');
      drama = JSON.parse(dramaJson) || dramaDefault;
    } else {
      drama = dramaDefault;
    }
    this.state = {
      perview: this.generatePinkx(drama)
        .replace(/(\s\s)*/g, '')
        .replace(/\n*/g, ''),
    };
    this.handleTextFiledChange = this.handleTextFiledChange.bind(this);
    this.handleButtonClick = this.handleButtonClick.bind(this);
  }

  generatePinkx(drama: Drama) {
    return `
<pinkx id="pinkx">
${this.generateTitle(drama)}
${this.generateCover(drama)}
${this.generateContent(drama)}
${this.generateLinks(drama.links)}
${this.generateState()}
</pinkx>
<link rel="stylesheet" type="text/css" media="screen" href="http://pinkx.uupin1.com/demo/main.css" />
    `;
  }

  generateTitle(drama: Drama) {
    return `
<section id="title">
<h1>${drama.title}</h1>
<h5>${drama.episode}</h5>
<p>${drama.subtitle}</p>
</section>
    `;
  }

  generateCover(drama: Drama) {
    return `
<section id="cover">
<imgChunk data-img=${drama.dramaPoster}></imgChunk>
</section>
    `;
  }

  generateContent(drama: Drama) {
    return `
<section id="content">
${this.generatePeople(drama.staff, 'staff', '制作组')}
${this.generatePeople(drama.cast, 'cast', '配音组')}
${this.generatePeople(drama.ed, 'ed', '片尾曲')}
</section>
    `;
  }

  generatePeople(people: Person[], className: string, name: string) {
    return `
<group class="${className}">
<h3>${name}</h3>
    ${people
      .map(person => {
        return `
<item>
<a ${person.weibo ? `href="${person.weibo}" target="_black"` : ' '} class="item">
<span class="job">${person.job}</span>
<span class="name">${person.name}</span>
${person.organization ? `<span class="org">${person.organization}</span>` : ''}
</a>
</item>
        `;
      })
      .join('')}
<item>
<a href="http://pinkx.uupin1.com" class="item" target="_black">
<span class="job">宣传</span>
<span class="name">游里&pinkX</span>
</a>
</item>
</group>
    `;
  }

  generateLinks(links: OuterChain[]) {
    return `
<section id="links">
<h3>链接</h3>
      ${links
        .map(link => {
          return `
<item>
<a href=${link.url} class="link" target="_black">${link.name}</a>
${link.other ? `<span class="other">${link.other}</span>` : ''}
</item>
          `;
        })
        .join('')}
</section>
    `;
  }

  generateState() {
    return `
<section id="state">
<h3>- 声明 -</h3>
<item>本作品仅供配音爱好者学习、交流使用，请勿进行二次修改和用于商业用途。</item>
<item>转载时请保留以上信息完整，谢谢合作！</item>
</section>
    `;
  }

  // 生成html代码
  previewCode() {
    return `
<!DOCTYPE html><html><head><meta charset="utf-8" />
<meta http-equiv="Content-Type" content="text/html; charset=gb2312"><title>PinkX - 小粉红模拟器</title>
<link rel="stylesheet" type="text/css" media="screen" href="/demo/jjwxc.min.css" /></head><body bgcolor="#FFE7F7" topmargin="0">
<section class="top"><div class="google-ad">广告区</div></section><section class="boardname">
<div class="left">面包屑导航</div><div class="right">登录信息</div></section>
<table width="760" border="0" align="center" cellspacing="0" style="table-layout:fixed;word-wrap:break-word;">
<tbody><tr><td bgcolor="#E8F3FF"><div id="msgsubject" style="float:left;">主题：小粉红模拟器
<font color="#999999" size="-1">[1]</font></div><div style="float:right;">
<span style="font-weight:bold;font-size:14px;color:blue;cursor:pointer;display:inline;" id="addfavorite" onclick="addfavorite(2, 5068113)">收藏该帖</span></div></td></tr>
<tr class="comment_5068113"><td><table width="100%" border="0" cellspacing="0" cellpadding="0">
<tbody><tr><td valign="bottom" width="350"><div class="image1" style="display:inline-block;"></div>
<br></td><td class="textbook"><div align="right"><img class="heartimg" src="/demo/img/heart.gif" width="10" height="9">
<a href="#"><font color="#0d7328" size="-1">【出版书广告区】</font></a></div></td></tr></tbody>
</table></td></tr><tr class="comment_5068113 gifffter"><td>
<table width="760" border="0" cellpadding="0" cellspacing="0" style="table-layout:fixed;word-wrap:break-word;">
<tbody><tr><td class="read"><div id="topic">
${this.state.perview}
</div></td></tr></tbody></table></td></tr><tr class="comment_5068113"><td class="authorname">
<div align="left"><font color="99CC00" size="-1">№0 </font><font color="99CC00">☆☆☆</font>PINKX
<font color="#999999">78adf6e4</font><font color="99CC00">于</font>2019-02-29 11:11:11留言
<font color="99CC00">☆☆☆</font></div></td></tr><tr class="comment_5068113"><td height="1">
<div align="right"></div></td></tr></tbody></table><div align="center">
<hr width="760" color="#99CC00" size="1"></div></body></html>
    `;
  }

  handleTextFiledChange(event: any) {
    const target = event.target;
    const value: any = target.value;
    this.setState({ perview: value });
  }

  handleButtonClick() {
    const e: any = document.getElementById('code');
    if (e) {
      e.select();
      document.execCommand('Copy');
    }
  }

  public render() {
    const perview: string = this.state.perview;
    return (
      <Layout>
        <Wrapper>
          <Card>
            <CardContent className={`card-title`}>预览</CardContent>
            <CardContent>
              <TextField
                id="code"
                fullWidth={true}
                variant="filled"
                label="生成的代码"
                name="edStr"
                value={perview}
                multiline
                rows={4}
                rowsMax={16}
                onChange={this.handleTextFiledChange}
              />
            </CardContent>
            <Arrow>
              <Button size="large" className={`button`} color="primary" onClick={this.handleButtonClick}>
                <FileCopySharpIcon />
                复制代码
              </Button>
              <Link to="/generate">
                <Button size="large" className={`button`}>
                  <ArrowBackIcon />
                  上一步
                </Button>
              </Link>
            </Arrow>
          </Card>
        </Wrapper>
        <BackBlack>
          <Screen id="screen" srcDoc={this.previewCode()}>
            {this.previewCode()}
          </Screen>
        </BackBlack>
      </Layout>
    );
  }
}
